@import "../../../sass/consts";

.auction_goods_detail {
  .top_tab {
    position: relative;
    padding: 0 25%;

    .msg_center {
      position: absolute;
      height: 1.5rem;
      top: .5rem;
      right: 1rem;
      z-index: 10;
    }
  }

  .contents {
    position: relative;

    > .am-tabs > .am-tabs-tab-bar-wrap {
      border-bottom: 1px solid #ddd;

      .am-tabs-default-bar {
        padding: 0 25%;
      }

      .am-tabs-default-bar-top .am-tabs-default-bar-tab::after {
        height: 0;
      }
    }

    .goods_detail {
      background-color: $bg1;

      .title {
        padding: .5rem;
      }

      .auction_status {
        font-size: .8rem;
        margin: .5rem 0;
        color: #666;

        span {
          padding: 0 .5rem;
          border-radius: .2rem;
          margin-right: .5rem;
        }
      }

      .price_num {
        font-size: .8rem;
        line-height: 1.5rem;
        color: #666;
      }
    }

    .auction_scenes {
      position: absolute;
      width: 100%;
      top: 0;
      left: 100%;
      z-index: 10;

      &.scenes1 {
        animation: hide_sence 0.5s linear;
        left: 100%;
      }

      &.scenes2 {
        animation: show_sence 0.5s linear;
        left: 0;
      }

      .leader {
        background-color: gray;
        padding: .5rem 1rem;

        img {
          width: 3rem;
          height: 3rem;
          border-radius: 50%;
        }

        .padding_lr {
          div {
            color: white;
          }

          span {
            display: inline-block;
            padding: 0 .5rem;
            border-radius: .3rem;
          }
        }

        .current {
          div {
            color: #999;
          }
        }
      }

      .list {
        background-color: black;
        opacity: 0.7;
        padding: 1rem;

        img {
          width: 2rem;
          height: 2rem;
          border-radius: 50%;
        }

        .scenes {
          align-items: flex-start;

          .padding_lr {
            div {
              font-size: .5rem;
              padding-bottom: .2rem;

              span {
                background-color: #333333;
                color: #999;
                margin-left: .5rem;
                border-radius: .2rem;
              }
            }

            .state5 {
              line-height: 2rem;
              border-radius: 1rem;
            }
          }

          span {
            display: inline-block;
            padding: 0 .5rem;
          }


        }

        .no_auction {
          color: white;
          text-align: center;
        }
      }

    }

  }

  .offer_opr {
    > * {
      line-height: 3rem;
      height: 3rem;
    }

    .do_offer {
      padding: 0 2rem;
    }
  }
}


@keyframes show_sence {
  from {
    left: 100%;
  }

  to {
    left: 0;
  }
}

@keyframes hide_sence {
  from {
    left: 0;
  }

  to {
    left: 100%;
  }
}
